import React, { Component, Fragment } from "react";
import axios from "./axios";
import { AreaChart } from "react-charts-d3";

class Downloads extends Component {
  state = {
    data: [],
    lineChartData: [],
  };

  componentDidMount() {
    var slug = localStorage.getItem("slug");
    const proxyurl = "https://cors-anywhere.herokuapp.com/";
    const url =
      "http://api.wordpress.org/stats/plugin/1.0/downloads.php?slug=" +
      slug +
      "&limit=15";
    axios.get(proxyurl + url).then((res) => {
      this.setState({ data: res.data });

      const nd = this.state.data;
      //console.log(nd);
      let arr = [];
      let count = 0;
      for (let [key, value] of Object.entries(nd)) {
        const d = new Date(key);
        const ye = new Intl.DateTimeFormat("en", { year: "2-digit" }).format(d);
        const mo = new Intl.DateTimeFormat("en", { month: "short" }).format(d);
        const da = new Intl.DateTimeFormat("en", { day: "2-digit" }).format(d);

        var dateFormatted = `${da} ${mo}`;
        // eslint-disable-next-line no-useless-concat
        count = count + 1;
        arr[count] = {
          x: dateFormatted,
          y: value,
        };
        var filterArr = arr.filter((n) => n); // Remove empty data

        //console.log(filterArr);
      }
      this.setState({ lineChartData: filterArr });
    });
  }

  constructor(props) {
    super(props);

    this.state = {
      //D3 Config
      axisConfig: {
        showXAxis: true,
        showXAxisLabel: true,
        xLabel: "X Axis",
        xLabelPosition: "right",
        showYAxis: true,
        showYAxisLabel: true,
        yLabel: "Y Axis",
        yLabelPosition: "bottom",
      },
      animation: {
        delay: 100,
        duration: 200,
      },
      noDataMessage: "No Data Available.",
      showGrid: true,
      strokeWidth: 3,
      pointSize: 3,
      colorScale: { from: "#2D87BB", to: "#7AC142" },
    };
  }

  render() {
    var lineData = this.state.lineChartData;
    if (!lineData) return <div className="loading"></div>;
    //============================

    var lineChartData = [{ values: lineData }];

    return (
      <Fragment>
        <AreaChart
          data={lineChartData}
          axisConfig={this.state.axisConfig}
          showGrid={this.state.showGrid}
          height={270}
          strokeWidth={this.state.strokeWidth}
          pointSize={this.state.pointSize}
          noDataMessage={this.state.noDataMessage}
          colorScale={this.state.colorScale}
        />
      </Fragment>
    );
  }
}
export default Downloads;
