import React, { Component, Fragment } from "react";
import axios from "./axios";
import { PieChart } from "react-charts-d3";
class ActiveVersion extends Component {
  state = {
    data: [],
  };

  componentDidMount() {
    var slug = localStorage.getItem("slug");
    const proxyurl = "https://cors-anywhere.herokuapp.com/";
    const url = "https://api.wordpress.org/stats/plugin/1.0/?slug=" + slug;
    axios.get(proxyurl + url).then((res) => {
      this.setState({ data: res.data });
      const d = this.state.data;
      let arr = [];
      let count = 0;
      for (let [key, value] of Object.entries(d)) {
        count = count + 1;
        arr[count] = {
          label: "v" + key,
          value: value,
        };
        var filterArr = arr.filter((n) => n); // Remove empty data
      }

      this.setState({ PieChartData: filterArr });
    });
  }
  constructor(props) {
    super(props);

    this.state = {
      //D3 Config
      width: 1000,
      height: 270,
      labelOffset: 120,
      colorScale: { from: "#7AC142", to: "#2D87BB" },
    };
  }

  render() {
    const d = this.state.data;
    const PieChartData = this.state.PieChartData;

    if (!d) return <div className="loading"></div>;
    if (!PieChartData) return <div className="loading"></div>;
    //console.log(PieChartData);

    const data = PieChartData;

    //console.log(data);
    return (
      <Fragment>
        <PieChart
          data={data}
          width={this.state.width}
          height={this.state.height}
          labelOffset={this.state.labelOffset}
          colorScale={this.state.colorScale}
        />
        {/* <table className="table" style={{ fontSize: 17 }}>
          <tbody>
            {Object.entries(d).map((t, k) => (
              <tr key={k}>
                <td>{t[0]}</td>
                <td>{t[1]}%</td>
              </tr>
            ))}
          </tbody>
        </table> */}
      </Fragment>
    );
  }
}
export default ActiveVersion;
